.Feature {
    position: relative;
    min-height: 50vw;
    width: 50vw;
    margin-left: 25vw;
    margin-top: 15px;
    -webkit-box-shadow: 0px 4px 10px 1px rgba(0, 0, 0, 0.329);
            box-shadow: 0px 4px 10px 1px rgba(0, 0, 0, 0.329);
    border-radius: 5px;
    padding-bottom: 25px;
    font-family: "omnes-pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.Feature.TripTunes {
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(236, 236, 236)), to(rgb(221, 221, 221)));
    background: -webkit-linear-gradient(rgb(236, 236, 236), rgb(221, 221, 221));
    background: -o-linear-gradient(rgb(236, 236, 236), rgb(221, 221, 221));
    background: linear-gradient(rgb(236, 236, 236), rgb(221, 221, 221));
    color: rgb(37, 37, 37);
}

.Feature.Chattr {
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(236, 236, 236)), to(rgb(221, 221, 221)));
    background: -webkit-linear-gradient(rgb(236, 236, 236), rgb(221, 221, 221));
    background: -o-linear-gradient(rgb(236, 236, 236), rgb(221, 221, 221));
    background: linear-gradient(rgb(236, 236, 236), rgb(221, 221, 221));
    color: rgb(37, 37, 37);
}

.Feature h2 {
    padding-top: 50px;
    font-weight: 1200;
    font-size: 48px;
    text-align: center;
    margin: 0;
    opacity: 1;
    position: relative;
    font-weight: 100;
}

.TechBox {
    position: absolute;
    top: 130px; 
    width: 100%;
    text-align: center;
}

@media (max-width: 1565px) {
    .Feature {
        width: 60vw;
        margin-left: 20vw;
    }
}
@media (max-width: 1300px) {
    .Feature {
        width: 70vw;
        margin-left: 15vw;
    }
}
@media (max-width: 1115px) {
    .Feature {
        width: 80vw;
        margin-left: 10vw;
    }
}
@media (max-width: 976px) {
    .Feature {
        width: 95vw;
        margin-left: 2.5vw;
    }
}

@media (max-width: 480px) {
    .Feature h2 {
        padding-top: 25px;
    }

    .Feature {
        width: 100vw;
        margin: 0;
        border-radius: 0;
        -webkit-box-shadow: none;
                box-shadow: none;
    }

    .TechBox {
        top: 95px; 
    }

    .Feature.TripTunes {
        background: rgb(236, 236, 236);
        color: rgb(37, 37, 37);
    }

    .Feature.Chattr {
        background: rgb(221, 221, 221);
        color: rgb(37, 37, 37);
    }
    
}


