.Info {
    width: 45%;
    border-radius: 5px;
    padding: 10px;
    display: inline-block;
    vertical-align: top;
}


.Info p {
    width: 100%;
}

.Info.BTS p {
    text-align: left;
}

.Info.BTS {
    width: 60%;
}

.Info i {
    position: relative;
    background-color: rgb(255, 208, 0);
    text-align: center;
    color: white;
    font-size: 36px;
    width: 50px;
    height: 44px;
    padding-top: 6px;
    border-radius: 50%;
    margin-right: 10px;
}

.Info b {
    font-size: 24px;
    font-weight: 900;
}

@media (max-width: 667px) {
    .Info, .Info.BTS {
        width: 80%;
    }
}
@media (max-width: 480px) {
    .Info {
        width: 95%;
        margin-top: -20px;
    }
    .Info.BTS {
        width: 95%;
        margin-left: -7.5%;
    }

    .Info b {
        font-size: 20px;
        margin-top: -2px;
    }

    .Info i {
        font-size: 24px;
        width: 35px;
        height: 29px;
    }

    .Info p {
        margin-top: 3px;
    }
}



