.Avatar {
    position: relative;
    top: 20vh;

    border-radius: 50%;
    will-change: margin-top, margin-left;
    -webkit-transition: margin-left 1000ms, margin-top 1000ms, height 1000ms, width 1000ms;
    -o-transition: margin-left 1000ms, margin-top 1000ms, height 1000ms, width 1000ms;
    transition: margin-left 1000ms, margin-top 1000ms, height 1000ms, width 1000ms;
}


@media (max-width: 500px) {
    .Avatar {
        top: 12vh;
    }
}





